body {
  margin: 10px;
  padding: 0;
  font-family: sans-serif;
  background: #f4f4f4; }

.clear {
  clear: both;
  content: ""; }

.game {
  max-width: 1200px;
  margin: 0 auto; }
  .game .game-info {
    font-family: Luckiest Guy, serif;
    font-size: 20px;
    font-style: normal;
    -webkit-font-feature-settings: normal;
            font-feature-settings: normal;
    font-variant: normal;
    font-weight: 700;
    line-height: 15.4px;
    margin-bottom: 20px;
    background: #136c0a;
    padding: 7px;
    text-align: center;
    color: #fff;
    min-height: 100px;
    border-radius: 7px; }
    .game .game-info .info1 {
      color: orange;
      font-size: 50px; }
    .game .game-info .info2 {
      color: #6acfff;
      font-size: 50px; }
    .game .game-info .info {
      display: block;
      margin-top: 15px; }
  .game .cell {
    background: #7b7b7b;
    border: 6px solid #895100;
    float: left;
    line-height: 45px;
    height: 110px;
    text-align: center;
    font-size: 40px;
    width: 110px;
    cursor: pointer;
    border-radius: 1px;
    color: #f6fcff;
    font-weight: 600; }
    .game .cell:focus {
      outline: none; }
  .game .hidden {
    background: center url("https://png.pngtree.com/element_origin_min_pic/16/07/04/14577a00cb2a1da.jpg"); }
  .game .is-flag,
  .game .is-mine {
    color: #fc543c; }

